import React, { useState } from "react";
import Card from "../UI/Card";
import classes from "./AddUser.module.css";
import Button from "../UI/Button";
import ErrorModal from "../UI/ErrorModal";
const AddUser = (props) => {
  const [enteredUsername, SetEnteredUsername] = useState("");
  const [enteredAge, SetEnteredAge] = useState(0);
  const [error, SetError] = useState();
  const addUserHandler = (event) => {
    event.preventDefault();
    if (enteredUsername.trim().length === 0 || enteredAge.length === 0) {
      SetError({
        title: "Invalid input",
        message: "Please enter a valid name or age ( no empty fields )",
      });
    }
    if (+enteredAge < 1) {
      SetError({
        title: "Invalid age",
        message: "Please enter a valid age",
      });
    }
    if (enteredAge > 1 && enteredUsername.trim().length >0) {
      const id = 1 + Math.floor(Math.random() * 1000);
      const user = { key: id, username: enteredUsername, age: enteredAge };
      props.addUser(user);
      SetEnteredUsername("");
      SetEnteredAge(0);
    }
  };
  const usernameChangeHandler = (event) => {
    SetEnteredUsername(event.target.value);
  };
  const ageChangeHandler = (event) => {
    SetEnteredAge(event.target.value);
  };
  const errorHandler = () => {
    SetError(null);
  };
  return (
    <div>
      {error && (
        <ErrorModal
          title={error.title}
          message={error.message}
          onOkay={errorHandler}
        />
      )}
      <Card className={classes.input}>
        <form onSubmit={addUserHandler}>
          <label htmlFor="username">Username</label>
          <input
            id="username"
            type="text"
            value={enteredUsername}
            onChange={usernameChangeHandler}
          />
          <label htmlFor="age">Age</label>
          <input
            id="age"
            type="number"
            value={enteredAge}
            onChange={ageChangeHandler}
          />
          <Button type="submit"> Add User </Button>
        </form>
      </Card>
    </div>
  );
};

export default AddUser;
